<template>
  <div class="Regist">
    <div class="header">
      <mt-header title="">
        <router-link to="/" slot="left">
          <mt-button icon="back"></mt-button>
        </router-link>
      </mt-header>
    </div>
    <div class="regist-warpper">
      <div class="input-warpper">
        <label></label>
        <mt-field placeholder="" type="text" v-model="username"></mt-field>
      </div>
      <div class="input-warpper">
        <label></label>
        <mt-field placeholder="" type="password" v-model="password"></mt-field>
      </div>
      <div class="input-warpper">
        <label></label>
        <mt-field placeholder="" type="password" v-model="checkpassword"></mt-field>
      </div>

      <div class="operate-warpper">
        <mt-button @click="regist()" type="primary" size="large" plain></mt-button>
      </div>
    </div>
  </div>
</template>
<style lang="scss">
.Regist {
  height: 100%;
  width: 100%;
  .header {
    .mint-header-title {
      font-size: 20px;
    }
  }
  .input-warpper {
    padding: 10px 20px 10px;
    text-align: left;
    .mint-cell-wrapper {
      margin-top: 5px;
      background-color: #d7d7d7;
      .mint-field-core {
        background-color: #d7d7d7;
      }
    }
  }
  .operate-warpper {
    padding: 20px;
  }
}
</style>
<script>
import { setTimeout } from "timers";
import { Toast } from "mint-ui";
import { MessageBox } from "mint-ui";
import { returnStatement } from "@babel/types";
import axios from "axios";
import urls from "../urls";
import { deflate } from "zlib";
export default {
  name: "Regist",
  data() {
    return {
      username: "",
      password: "",
      checkpassword: ""
    };
  },
  methods: {
    regist() {
      //TODO:
      if (!this.username || !this.password || !this.checkpassword) {
        MessageBox("", "");
        return;
      } else {
        if (this.password != this.checkpassword) {
          MessageBox("", "");
          return;
        } else {
          axios
            .post(urls.regist, {
              username: this.username,
              password: this.password
            })
            .then(res => {
              switch (res.data.state) {
                case 0:
                  //Toast
                  let instance = Toast("");
                  setTimeout(() => {
                    instance.close();
                  }, 1500);
                  //
                  this.$router.push({ path: "/justclick" });
                  break;
                case -1:
                  MessageBox("", "");
                  break;
                default:
                  MessageBox("", "");
              }
            });
        }
      }
    }
  }
};
</script>